import React from 'react'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import { Switch, Route, Link } from 'react-router-dom'
import './App.css'
import logo from '../fd-logo.svg'
import MyPage from "../MyPage/MyPage"
import Resume from "../Resume/Resume"

function App() {
  return (
    <div className="App">
      <Navbar expand="md" bg="dark" variant="dark" sticky="top">
        <Navbar.Brand as={Link} to="/">
          <img
            alt="Feroz Damani Logo"
            src={logo}
            width="32"
            height="32"
          />{' '}
          Feroz Damani - Frontend Developer
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav" className="justify-content-end">
          <Nav>
            <Nav.Link as={Link} to="/my-resume">
              <strong>
                Resume
              </strong>
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
      <div>
      <Switch>
        <Route exact path='/' component={MyPage} />
        <Route exact path='/my-resume' component={Resume} />
        <Route render={function () {
          return <p>Not found</p>
        }} />
      </Switch>
      </div>
    </div>
  );
}

export default App
